.movie-grid{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    padding: 20px;
    width: 100%;
    max-width: 1300px;
    margin: auto;
    margin-bottom: 3rem;

}
.movie-grid img{
    width: 100%;
}
.loadmore{
    width: 100%;
    max-width: 1300px;
    margin: auto;
    text-align: right;
    padding: 10px 30px;
}

.outline-small{
    padding: 10px 20px;
    background-color: transparent;
    border: 2px solid #fff;
    border-radius: 10px;
    color: #fff;
    text-transform: capitalize;
    box-shadow: 0px 0px 5px 1px #ffffff42;
}
.outline-small:hover{
    cursor: pointer;
    background-color: #fff;
    color: #000;
}

.movie-search{
    width:100%;
    max-width: 1300px;
    margin: auto;
    padding: 10px 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.movie-search button{
    padding:9px 20px;
    border-radius: 30px;
    background-color: transparent;
    border: 1px solid #fff;
    box-shadow: 0px 0px 5px 1px #f1f1f156;
    color: #fff;
}
.movie-search button:hover{
    cursor: pointer;
    color: #000;
    background-color: #fff;
}


@media screen and (max-width:450px) {
    .movie-grid{
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    }
}