
.head-section{
    width: 100%;
    max-width: 1300px;
    margin: auto;
    padding: 10px 20px;
  }
  .head-section>:first-child{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px;
  }
  
  .head-section .small{
    padding: 10px !important;
    font-size: 1.1rem !important;
  }

  .btn-more{
    padding: 10px 20px;
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 30px;
  }
  .btn-more:hover{
    cursor: pointer;
    background-color: #fff;
    color: #000;
  }