.banner{
    height: 50vh;
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.banner::before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(rgba(27, 27, 27, 0.644), #000);
}


.movieContentContainer{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    margin-top: -100px;
    position: relative;
    padding: 0 2rem;
    gap: 20px;
}

.moviePoster{
    /* flex: 1; */
    width: 300px;
    margin-bottom: 50px;
}
.contentImg{

    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 30px;
    padding-top: 135%;
}

.content-info{
    width: 70%;
    margin:  10px auto;
    position: relative;
    
}

.contentTitle{
    font-size: 4rem;
    
    line-height: 1;
    margin-bottom: 20px;

}

.genres{
    margin-left: 0.5rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;

}

.genreItem{
    padding: 0.5rem 1.5rem;
    border: 1px solid #fff;
    border-radius: 30px;
    font-size: 0.8rem;
    font-weight: 600;
    margin-left: 10px;
    background-color: #0f0f0f;
}

.dOverview{
    width: 80%;
    margin: 20px 0;
    opacity: 0.8;
}

.castDetail{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(90px,1fr));
    gap: 10px;
}

.castHeader h2{
    padding: 10px 0;
}
.castImg{
    padding-top: 160px;
    background-size: cover;
    margin-bottom: 0.5rem;
}
.castName{
    font-size: 0.8rem;
}
.line{
    width: 80%;
    max-width: 1300px;
    
    margin: 20px auto;
    height: 2px;
    background-color: #fff;
}

.video{
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.videoContainer{
    width: 100%;
    max-width: 1300px;
    margin: 50px auto;

}


.videoTitle{
    width: 80%;
    margin: auto;
    padding: 10px;
    font-size: 1.2rem;
    text-align: center;
}

.similar{
    width: 100%;
    max-width: 1300px;
    margin: auto;
    padding: 10px 20px;
}

.similarHeader h2{
    font-size: 2rem;
    font-weight: 600;
padding: 20px;
}

@media screen and (max-width:1000px) {
    .contentTitle{
        font-size: 3rem;
    }
    .dOverview{
        width: 100%;
    }
}
@media screen and (max-width:768px) {
    .contentTitle{
        font-size: 2.5rem;
    }
}
@media screen and (max-width:550px) {
    .contentTitle{
        font-size: 2rem;
    }

    .moviePoster{
        display: none;
    }
    .content-info{
        width: 100%;
        text-align: center;
    }
    .genres{
        justify-content: center;
    }
    /* .overview{
        font-size: 0.8rem;
    } */
}