.page-header{
    padding: 100px 0 2rem;
    text-align: center;
    margin-bottom: 2rem;
    /* margin-top: 50px; */
    background-image: url('./footer-bg.jpg');
    position: relative;
    background-position:  top;
    background-size: cover;
    background-repeat: no-repeat;
}
.page-header h2{
    position: relative;
    z-index: 99;
}
.page-header::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to top, #0f0f0f,rgba(#000, 0));
}